.footer {
    text-align: center;
    background-color: #fff;
    padding: 20px 0 0 0;
    border-top: 10px solid #783f04;

    .sponsor-logos {
        padding: 20px 10px;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .link-image {
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            width: 250px;
            height: 250px;
        }

        .geocenter {
            background-image: url('./assets/geoalogo.jpg');
        }
    }

    .sponsor-section {
        display: flex;
        flex-direction: column;
    }
    .sponsor-section > h2 {
        margin-bottom: 1em;
    }

    .copyright-text {
        margin-bottom: 0;
        padding: 20px;
    }
    .faq-section .card {
        border: 1px solid rgba(0, 0, 0, 0.125);
    }
    .faq-header {
        display: flex;
        background-color: inherit !important;
        border: 0;
        border-bottom: 0px !important;
    }
    .faq-description {
        display: flex;
        padding-left: 32px !important;
        text-align: justify;
    }
}
